.navbar__button{
font-style: normal;  
font-weight: normal;   /*400*/
font-size: 18px;
line-height: 1.2;
color: #A9A9A9;

text-decoration: none;
transition: opacity .4s;
border: 0px;
cursor: pointer;
background-color: #000;

padding: 0;
margin: 0;
}



.navbar__button:hover {
	opacity: .6;
}